import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'
import { $http } from '@escook/request-miniprogram'

uni.$http = $http

$http.baseUrl = 'http://api.zhuishushenqi.com/'

// 请求开始之前做一些事情
$http.beforeRequest = function (options) {
  uni.showLoading({
    title: '数据加载中...',
  })
}


$http.afterRequest = function () {
  uni.hideLoading()
}

uni.$showMsg = function(title='数据请求失败',duration = 1500,icon = 'none'){
	uni.showToast({
		title,
		duration,
		icon
	})
}

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()